@font-face {
  font-family: "SuisseIntl";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/SuisseIntl-Regular.woff2") format("woff2"), url("../fonts/SuisseIntl-Regular.woff") format("woff");
}
@font-face {
  font-family: "SuisseIntl";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/SuisseIntl-Medium.woff2") format("woff2"), url("../fonts/SuisseIntl-Medium.woff") format("woff");
}
* {
  box-sizing: border-box;
}

body {
  font-family: "SuisseIntl", "Arial", sans-serif;
  font-size: 14px;
  line-height: 150%;
  color: #151515;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
}

input::placeholder,
textarea::placeholder {
  color: #b2b8c0;
}

.auth {
  min-height: 100vh;
  padding: 40px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.auth__wrapper {
  padding-bottom: 80px;
  padding-left: 5vmin;
  padding-right: 5vmin;
  width: 100%;
  min-width: 320px;
}
@media (min-width: 768px) {
  .auth__wrapper {
    padding-left: 16px;
    padding-right: 16px;
    max-width: 600px;
  }
}
.auth__logo {
  text-align: center;
  margin-bottom: 2.5vmin;
}
@media (min-width: 768px) {
  .auth__logo {
    margin-bottom: 8px;
  }
}
.auth__logo img {
  width: 20vmin;
  height: auto;
}
@media (min-width: 768px) {
  .auth__logo img {
    width: 64px;
  }
}
.auth__title {
  font-size: 6.25vmin;
  line-height: 150%;
  font-weight: 500;
  text-align: center;
  margin: 0 0 7.5vmin;
}
@media (min-width: 768px) {
  .auth__title {
    font-size: 20px;
    margin: 0 0 24px;
  }
}
.auth__form-fields {
  display: flex;
  flex-direction: column;
  gap: 2.5vmin;
  margin-bottom: 5vmin;
}
@media (min-width: 768px) {
  .auth__form-fields {
    margin-bottom: 16px;
    gap: 8px;
  }
}
.auth__input-field input {
  width: 100%;
  font-size: 4.375vmin;
  padding: 3.75vmin 5vmin;
  border: 1px solid #e6e6ef;
  transition: border-color 0.3s;
}
@media (min-width: 768px) {
  .auth__input-field input {
    font-size: 14px;
    padding: 12px 16px;
  }
}
.auth__input-field input:focus {
  border-color: #b5bbc3;
}
.auth__input-field.is--invalid input {
  border-color: #f25248;
}
.auth__input-field.is--invalid .auth__input-error {
  display: block;
}
.auth__form-submit {
  background: #fed664;
  width: 100%;
  border: none;
  padding: 4.375vmin;
  font-size: 4.375vmin;
}
@media (min-width: 768px) {
  .auth__form-submit {
    padding: 14px;
    font-size: 14px;
    width: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
    min-width: 250px;
  }
}
.auth__form-submit:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.auth__input-error {
  display: none;
  color: #f25248;
  margin: 1.25vmin 0 0;
  font-size: 3.75vmin;
}
@media (min-width: 768px) {
  .auth__input-error {
    font-size: 12px;
    margin: 4px 0 0;
  }
}